.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.outerTile {
  padding: 0.5rem;
}

.tile {
  border-radius: 1rem;
  background-color: rgb(70, 70, 70);
  padding: 1rem;
  font-size: xx-large;
  height: 3rem;
}

.left {
  float: left;
  min-width: 5rem;
  vertical-align: middle;
  display: flex;
}

.right {
  float: right;
  min-width: 5rem;
  vertical-align: middle;
  display: flex;
}

.logo {
  height: 3rem;
  align-self: center;
}

.text {
  height: 3rem;
  align-self: center;
  padding-left: 1rem;
  padding-right: 1rem;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
